<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Halaman TO Kedinasan</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>

    <ion-content :fullscreen="true" class="exam-page">
      <ion-content class="ion-padding">
        <ion-card style="background-color: #faf8f2">
          <div v-if="questions.length > 0 && !loading">
            <center>
              <ion-card v-if="hasTakenExam" class="ion-text-center">
                <ion-card-content>
                  <ion-card-title>Hasil Ujian</ion-card-title>
                  <p>Nilai Anda: <strong>{{ score }}</strong></p>
                  <ion-button
                    expand="block"
                    color="success"
                    @click="goHome"
                    style="margin-top: 10px;"
                  >
                    Kembali ke Halaman Utama
                  </ion-button>
                  <ion-button
                    expand="block"
                    color="warning"
                    @click="resetExam"
                    style="margin-top: 10px;"
                  >
                    TO Ulang
                  </ion-button>

                  <ion-button
                    expand="block"
                    color="danger"
                    @click="goToExplanationPage"
                    style="margin-top: 10px;"
                  >
                    Lihat Pembahasan
                  </ion-button>
                </ion-card-content>
              </ion-card>
            </center>
          </div>
          <ion-spinner v-if="loading" name="crescent"></ion-spinner>
        </ion-card>

        <div v-if="!hasTakenExam && currentQuestionIndex < questions.length">
          <ion-button style="color: white" expand="block" @click="openQuestionModal">
            Pilih Soal
          </ion-button>
          <ion-card>
            <div class="timer-badge">
              <ion-badge style="color: white">{{ formatTime(timer) }}</ion-badge>
            </div>
            <ion-card-header>
              <ion-card-title>
                <div v-html="questions[currentQuestionIndex].question"></div>
              </ion-card-title>
            </ion-card-header>
            <ion-card-content>
              <div v-if="questions[currentQuestionIndex].imageUrl">
                <ion-img :src="questions[currentQuestionIndex].imageUrl"></ion-img>
              </div>
              <div v-if="questions[currentQuestionIndex].videoUrl">
                <video :src="questions[currentQuestionIndex].videoUrl" controls></video>
              </div>
            </ion-card-content>
          </ion-card>

          <div class="options">
            <ion-card
              v-for="(option, key) in questions[currentQuestionIndex].options"
              :key="key"
              @click="selectOption(key)"
              :class="{ selected: answers[currentQuestionIndex] === key }"
            >
              <ion-card-content>{{ option }}</ion-card-content>
            </ion-card>
          </div>

          <ion-button style="color: white" expand="block" @click="nextQuestion">
            {{ currentQuestionIndex === questions.length - 1 ? "Finish" : "Next" }}
          </ion-button>
        </div>
      </ion-content>

      <ion-alert
        :is-open="isAlertOpen"
        header="Hasil Ujian"
        :message="'Nilai Anda: ' + score"
        :buttons="[{
          text: 'OK',
          handler: storeScoreAndGoHome
        }, 
        { 
          text: 'Ujian Ulang', 
          handler: resetExam 
        }]"
        @did-dismiss="() => (isAlertOpen = false)"
      >
      </ion-alert>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>

      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Pilih Soal</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal">Tutup</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-list>
            <ion-item
              v-for="(question, index) in questions"
              :key="index"
              @click="jumpToQuestion(index)"
            >
              <ion-label>Soal {{ index + 1 }}</ion-label>
            </ion-item>
          </ion-list>
        </ion-content>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonItem,
  IonLabel,
  IonButton,
  IonToast,
  IonBackButton,
  IonBadge,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonImg,
  IonModal,
  IonList,
  IonButtons,
  IonAlert,
} from "@ionic/vue";
import { ref, onMounted, watchEffect } from "vue";
import {
  getFirestore,
  doc,
  getDoc,
  collection,
  getDocs,
  setDoc,
  serverTimestamp,
} from "firebase/firestore";
import { useRoute, useRouter } from "vue-router";
import { getAuth } from "firebase/auth";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const route = useRoute();
const router = useRouter();
const jadwalId = ref(route.params.jadwalId);
const questions = ref([]);
const currentQuestionIndex = ref(0);
const answers = ref([]);
const score = ref(0);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const timer = ref(0);
const intervalId = ref(null);
const hasTakenExam = ref(false);
const isModalOpen = ref(false);
const isAlertOpen = ref(false);
let initialDuration = 0;

const goToExplanationPage = () => {
  console.log("Navigating to Explanation Page with jadwalId:", jadwalId.value);
  router.replace({ name: 'explanation', params: { jadwalId: jadwalId.value } });
};
const formatTime = (seconds) => {
  const minutes = Math.floor(seconds / 60);
  const secs = seconds % 60;
  return `${minutes.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`;
};

const checkExamStatus = async () => {
  const user = auth.currentUser;
  if (user) {
    try {
      const resultDoc = await getDoc(doc(db, `examResults/${user.uid}/results`, jadwalId.value));
      if (resultDoc.exists()) {
        score.value = resultDoc.data().score;
        hasTakenExam.value = true;
        isAlertOpen.value = true;
      }
    } catch (error) {
      console.error("Error checking exam status:", error);
    }
  }
};

const fetchQuestions = async () => {
  const user = auth.currentUser;
  if (user) {
    if (!jadwalId.value) {
      console.error("jadwalId is undefined");
      return;
    }

    try {
      const jadwalDoc = await getDoc(doc(db, "jadwalkedinasan", jadwalId.value));
      if (jadwalDoc.exists()) {
        const jadwalData = jadwalDoc.data();
        const questionsSnapshot = await getDocs(collection(db, "soalkedinasan"));
        questions.value = questionsSnapshot.docs
          .map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }))
          .filter((question) => jadwalData.soal && jadwalData.soal.includes(question.id));

        initialDuration = jadwalData.durasi * 60;
        timer.value = initialDuration;

        const answersDoc = await getDoc(doc(db, `examResults/${user.uid}/answers`, jadwalId.value));
        if (answersDoc.exists()) {
          answers.value = answersDoc.data().answers || new Array(questions.value.length).fill("");
        } else {
          answers.value = new Array(questions.value.length).fill("");
        }

        startTimer();
      } else {
        console.error("Dokumen jadwal tidak ditemukan di koleksi 'jadwalkedinasan'.");
      }
    } catch (error) {
      console.error("Error fetching documents:", error);
    }
  } else {
    console.error("User is not authenticated");
  }
};

const saveAnswersToFirestore = async () => {
  const user = auth.currentUser;
  if (user) {
    try {
      await setDoc(doc(db, `examResults/${user.uid}/answers`, jadwalId.value), {
        answers: answers.value,
        updatedAt: serverTimestamp(),
      });
      showToast("Jawaban disimpan!", "success");
    } catch (error) {
      console.error("Error saving answers:", error);
      showToast("Gagal menyimpan jawaban.", "danger");
    }
  }
};

const openQuestionModal = () => {
  isModalOpen.value = true;
};

const closeModal = () => {
  isModalOpen.value = false;
};

const jumpToQuestion = (index) => {
  if (index >= 0 && index < questions.value.length) {
    currentQuestionIndex.value = index;
    closeModal();
  } else {
    console.error("Invalid question index:", index);
  }
};

const startTimer = () => {
  intervalId.value = setInterval(() => {
    if (timer.value > 0) {
      timer.value--;
    } else {
      clearInterval(intervalId.value);
      finishExam();
    }
  }, 1000);
};

const resetExam = () => {
  currentQuestionIndex.value = 0;
  answers.value = new Array(questions.value.length).fill("");
  timer.value = initialDuration;
  hasTakenExam.value = false;
  clearInterval(intervalId.value);
  startTimer();
};

const nextQuestion = () => {
  if (currentQuestionIndex.value < questions.value.length - 1) {
    currentQuestionIndex.value++;
  } else {
    finishExam();
  }
};

const finishExam = () => {
  calculateScore();
  isAlertOpen.value = true;
  hasTakenExam.value = true;
};

const calculateScore = () => {
  const correctAnswers = questions.value.reduce((acc, question, index) => {
    if (question.correctAnswer === answers.value[index]) {
      return acc + 1;
    }
    return acc;
  }, 0);

  score.value = correctAnswers * 5;
};

const storeScoreAndGoHome = async () => {
  const user = auth.currentUser;
  if (user) {
    try {
      await setDoc(doc(db, `examResults/${user.uid}/results`, jadwalId.value), {
        userId: user.uid,
        jadwalId: jadwalId.value,
        score: score.value,
        createdAt: serverTimestamp(),
      });
      showToast("Nilai berhasil disimpan!", "success");
      router.push('/');
    } catch (error) {
      console.error("Error saving score:", error);
      showToast("Gagal menyimpan nilai.", "danger");
    }
  }
};

const selectOption = (option) => {
  answers.value[currentQuestionIndex.value] = option;
  saveAnswersToFirestore();
};

const showToast = (message, color) => {
  toast.value = { isOpen: true, message, duration: 2000, color };
};

onMounted(() => {
  checkExamStatus();
  fetchQuestions();
});

watchEffect(() => {
  checkExamStatus();
});

const goHome = () => {
  router.push('/');
};
</script>

<style>
.exam-page {
  position: relative;
}
.timer-badge {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}
.options {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.options ion-card {
  flex: 1 1 calc(50% - 10px);
  cursor: pointer;
  text-align: center;
}
.options ion-card.selected {
  border: 2px solid var(--ion-color-primary);
}
</style>
